<template>
  <div style="min-height: 600px" v-loading="loading">
    <el-card shadow="never">
      <div slot="header" class="clearfix">
        <span>关于我</span>
      </div>
      <div class="user-profile">
        <div class="box-center">
          <div class="demo-image__preview">
            <el-image style="width: 100px; height: 150px" :src="url" :preview-src-list="srcList"></el-image>
          </div>
        </div>
      </div>
      <div class="user-bio">
        <div class="user-education user-bio-section">
          <div class="user-bio-section-header">
            <span style="padding-left: 4px;">EDUCATION</span>
          </div>
          <div class="user-bio-section-body">
            <div class="text-muted">湖南省15级怀化学院计算机科学与工程学院</div>
          </div>
        </div>

        <div class="user-education user-bio-section">
          <div class="user-bio-section-header">
            <span style="padding-left: 4px;">INTRODUCE</span>
          </div>
          <div class="user-bio-section-body">
            <div class="text-muted">Name：谭风定</div>
          </div>
          <div class="user-bio-section-body user-hegiht">
            <div class="text-muted">QQ：1466939440</div>
          </div>
          <div class="user-bio-section-body user-hegiht">
            <div class="text-muted">Eamil：1466939440@qq.com</div>
          </div>
          <div class="user-bio-section-body user-hegiht">
            <div class="text-muted">简述：到了{{getNewYear}},自己还是要幸福！</div>
          </div>
        </div>

        <div class="user-skills user-bio-section">
          <div class="user-bio-section-header">
            <span>PROFESSIONAL QUALITIES</span>
          </div>
          <div class="user-bio-section-body">
            <div class="progress-item">
              <span>HTML</span>
              <el-progress :percentage="70" />
            </div>
            <div class="progress-item">
              <span>JavaScript</span>
              <el-progress :percentage="65" />
            </div>
            <div class="progress-item">
              <span>Css</span>
              <el-progress :percentage="65" />
            </div>
          </div>
        </div>

        <div class="user-education user-bio-section">
          <div class="user-bio-section-header">
            <span style="padding-left: 4px;">Tag</span>
          </div>
          <div class="user-bio-section-body user-hegiht">
            <div class="text-muted btn btn-1" v-for="(item,index) in tag" :key="index">
              <svg>
                <rect x="0" y="0" fill="none" width="100%" height="100%" />
              </svg>
              {{item}}
            </div>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import ProjectApi from "@/api/project";
export default {
  data() {
    return {
      url: "/static/tfd.jpg",
      srcList: ["/static/tfd.jpg"],
      loading: false,
      text: "",
      tag: [
        "Vue",
        "UniApp",
        "element",
        "Vuex",
        "js",
        "webpack",
        "微信小程序",
        "css",
      ],
      getNewYear: 0,
    };
  },
  mounted() {
    this.loading = true;
    this.getNewYear = new Date().getFullYear();
    ProjectApi.getBlogReadme()
      .then((response) => {
        let result = response.data;
        let base64 = require("js-base64").Base64;
        this.text = base64.decode(result.content);
      })
      .then(() => (this.loading = false))
      .catch(() => (this.loading = false));
  },
};
</script>

<style scoped>
.text-muted {
  color: #777;
}
.box-center {
  margin: 0 auto;
  display: table;
}
.user-bio {
  margin-top: 20px;
  color: #606266;
}
.user-bio-section {
  font-size: 14px;
  padding: 15px 0;
}
.user-hegiht {
  margin-top: 10px;
}
.user-bio-section-header {
  border-bottom: 1px solid #dfe6ec;
  padding-bottom: 10px;
  margin-bottom: 10px;
  font-weight: bold;
}
.user-name {
  font-weight: normal;
}
.box-center {
  padding-top: 10px;
}
.user-role {
  padding-top: 10px;
  font-weight: 400;
  font-size: 14px;
}
.el-table {
  border-top: 1px solid #dfe6ec;
}
.box-social {
  padding-top: 30px;
}
.user-follow {
  padding-top: 20px;
}
.btn {
  padding: 0;
  text-align: center;
  color: black;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 35px;
  margin: 0 0 2em;
  max-width: 100px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
}
@media (min-width: 700px) {
  .btn {
    margin: 0 1em 1em;
  }
}
.btn:hover {
  text-decoration: none;
}
.btn-1 {
  font-weight: 100;
}
.btn-1 svg {
  height: 35px;
  left: 0;
  position: absolute;
  width: 100%;
}
.btn-1 rect {
  fill: none;
  stroke: black;
  stroke-width: 2;
  stroke-dasharray: 422, 0;
  transition: all 0.35s linear;
}

.btn-1:hover {
  background: rgba(225, 51, 45, 0);
  font-weight: 900;
  letter-spacing: 1px;
}
.btn-1:hover rect {
  stroke-width: 5;
  stroke-dasharray: 15, 310;
  stroke-dashoffset: 48;
  transition: all 1.35s cubic-bezier(0.19, 1, 0.22, 1);
}
</style>



