<template>
  <div style="border-top: 1px #e1e4e8 solid !important;padding: 45px 0px 45px 0px;">
    <el-row>
      <el-col :span="10">
        <div>
          © {{getNewYear}} actior&emsp;&emsp;
          <a
            href="https://github.com/actior"
            target="_blank"
          >Profile</a>&emsp;&emsp;
        </div>
      </el-col>
      <el-col :span="4">
        <div style="text-align: center;font-size: 18px">
          <el-tooltip class="item" effect="dark" content="作者定位在深圳" placement="top">
            <i class="el-icon-location-outline"></i>
          </el-tooltip>
        </div>
      </el-col>
      <el-col :span="10">
        <div style="float: right;">
          <a href="https://segmentfault.com/u/45dujiaodefengjing" target="_blank">Segmentfault</a>&emsp;&emsp;
          <a href="https://juejin.im/user/5bac588ae51d450e7762e0b6" target="_blank">掘金</a>&emsp;&emsp;
          <a href="https://leetcode-cn.com/u/na-zi-mie-2/" target="_blank">LeetCode</a>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      getNewYear: 0,
    };
  },
  mounted() {
    this.getNewYear = new Date().getFullYear();
  },
  methods: {},
};
</script>



